
/* for footer */
.footer-dark {
    margin-top: 10px;
    color: #f0f9ff;
    background-color: #000000;
    margin-bottom: 10px;
  }
  
  .footer-dark h3 {
    margin-top: 0;
    margin-bottom: 12px;
    font-weight: bold;
    font-size: 16px
  }
  
  .footer-dark ul {
    padding: 0;
    list-style: none;
    line-height: 1.6;
    font-size: 20px;
    margin-bottom: 0
  }
  
  .footer-dark ul a {
    color: inherit;
    text-decoration: none;
    opacity: 0.6
  }
  
  .footer-dark ul a:hover {
    opacity: 1
  }
  
  @media (max-width:767px) {
    .footer-dark .item:not(.social) {
        text-align: center;
        padding-bottom: 20px
    }
  }
  
  .footer-dark .item.text {
    margin-bottom: 36px
  }
  
  @media (max-width:767px) {
    .footer-dark .item.text {
        margin-bottom: 0
    }
  }
  
  .footer-dark .item.text p {
    opacity: 0.6;
    margin-bottom: 0
  }
  
  .footer-dark .item.social {
    text-align: center
  }
  
  @media (max-width:991px) {
    .footer-dark .item.social {
        text-align: center;
        margin-top: 15px
    }
  }
  
  .footer-dark .item.social>a {
    font-size: 20px;
    width: 36px;
    height: 36px;
    line-height: 36px;
    display: inline-block;
    text-align: center;
    border-radius: 50%;
    box-shadow: 0 0 0 1px rgba(255, 255, 255, 0.4);
    margin: 0 8px;
    color: #fff;
    opacity: 0.75
  }
  
  .footer-dark .item.social>a:hover {
    opacity: 1
  }
  
  
  .footer-dark .copyright {
    text-align: center;
    padding-top: 24px;
    opacity: 0.3;
    font-size: 15px;
    margin-bottom: 0
  }
  
  .footer-container {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    margin-top: 15px;
    margin-bottom: 20px;
  }
  
  .footer-container > div {
    width: 30%;
    text-align: center;
    padding: 10px;
    height: 150px;
  }